import React from 'react';
import styles from './RunningLine.module.css';

const runningLineData = [
  {
    category: 'SOFA recruitment',
    nicknames: [
      {
        name: 'LeftexDean',
        discordLink: 'https://discord.com/invite/leftexdean',
      },
      { name: 'ShibyUwU', discordLink: 'https://discord.com/invite/shibyuwu' },
    ],
  },
  {
    category: 'BOFA recruitment',
    nicknames: [
      {
        name: 'Skringlord',
        discordLink: 'https://discord.com/invite/skringlord',
      },
      { name: 'YmMingg', discordLink: 'https://discord.com/invite/ymmingg' },
      { name: 'LukaRaos', discordLink: 'https://discord.com/invite/lukaraos' },
    ],
  },
  {
    category: 'ROFA recruitment',
    nicknames: [
      { name: 'Ragu', discordLink: 'https://discord.com/invite/ragu' },
      { name: 'SUGMA', discordLink: 'https://discord.com/invite/sugma' },
      { name: 'YmMingg', discordLink: 'https://discord.com/invite/ymmingg' },
    ],
  },
];

// Create a new array that repeats the original data three times using the spread operator
const multipliedArray = [
  ...runningLineData,
  ...runningLineData,
  ...runningLineData,
];

export default function RunningLine() {
  return (
    <div className={styles.runningLineContainer}>
      <div className={styles.runningLine}>
        {multipliedArray.map((item, index) => (
          <React.Fragment key={index}>
            <a
              href='/home'
              className={styles[item.category.toLowerCase().replace(' ', '')]}
            >
              {item.category}
            </a>
            {' - '}
            {item.nicknames.map((nickname, i) => (
              <React.Fragment key={i}>
                <a href={nickname.discordLink} className={styles.nickname}>
                  {nickname.name}
                </a>
                {i < item.nicknames.length - 1 && ', '}
              </React.Fragment>
            ))}
            {index < multipliedArray.length - 1 && ' | '}
          </React.Fragment>
        ))}
      </div>
    </div>
  );
}
